.App {
  text-align: center;
  background: linear-gradient(224.15deg, #323643 0%, #111622 100%);
  font-family: 'Roboto', sans-serif;
  color:white;
  z-index:1;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  position: relative;
  width:70%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  /* font-size: calc(10px + 2vmin); */
  color: white;
  margin:0px auto;
  z-index: 2;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.survey_title{
  font-size: 40px;
  color:orange;
  text-align: left;
  width:100%;
  font-weight: bold;
  display: flex;
  flex-direction: row;
}

.survey_title div{
  padding:5px;
}

.question_title{
  font-weight: normal;
}

.title{
  font-weight: bold;
}

.background_wrap{
  position: relative;
  z-index:0;
}

.background_wave{
  position:absolute;
  bottom:0px;
  right:0px;
  width:100%;
  z-index:0;
}

.navbar{
  display: flex;
  justify-content: space-between;
  padding:20px 0px;
}

.navbar-logos{
  display: flex;
}

.navbar_title{
  font-weight: 500;
  font-size: 24px;
}

.logo{
  max-height:50px;
  max-width:200px;
  margin:0px 20px;
}

.content-wrap{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height:80vh;
}

.content-title{
  font-size: 50px;
  padding:20px;
  font-weight: 100;
}

.content-subtitle{
  font-size: 30px;
  padding:20px;
}

.terms_wrap{
  display: flex;
  padding:20px;
}

.terms_text{
  font-size: 18px;
  font-weight: normal;
  display: flex;
  justify-content: center;
  align-items: center;
}

.terms_and_conditions{
  padding:0px 5px;
}

.get_started{
  padding:20px;
  font-size: 18px;
}

.get_started_button{
  background: linear-gradient(240.71deg, #FFCB52 0%, #FF7B02 100%);
  border-radius:30px;
  padding:10px 30px;
  text-decoration: none;
  color:white;
  cursor: pointer;
}

.content_checkbox_wrap{
  padding-right:5px;
}

.intro{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width:80%;
  margin:0 auto;
  min-height: 80vh;
}

.button_wrap{
  padding:20px;
  display: flex;
  justify-content: center;
  width:100%;
}

.next_button{
  padding: 10px 30px;
  border-radius: 30px;
  margin:20px;
  background: linear-gradient(240.71deg, #FFCB52 0%, #FF7B02 100%);
  font-size: 18px;
  cursor: pointer;
  text-decoration: none;
  color:white;
}

.prev_button{
  padding: 10px 30px;
  border-radius: 30px;
  background: grey;
  margin:20px;
  font-size: 18px;
  cursor: pointer;
  text-decoration: none;
  color:white;
}

.videoplayer{
  border-radius: 10px;
  box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.212);
  outline:none;
}

.details{
  padding:30px;
}

.form{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

}

.form-item{
  width:40%;
  margin:50px 30px;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}

input[type=text]{
  color:white;
}

.form-item-input{
  background:transparent;
  width:100%;
  font-size: 18px;
  border:none;
  border-bottom:grey 1px solid;
  outline:none;
  padding:5px 0px;
}

.form-item-select{
  background: transparent;
  width:100%;
  color:white;
  font-size: 18px;
  border:none;
  border-bottom:grey 1px solid;
  outline:none;
  padding:5px 0px;
}

#option{
  background:  #323643 ;
  padding:5px;
  font-size: 16px;
}

.details_title{
  text-align: left;
  padding:30px 30px;
  display: flex;
  font-size: 30px;
  font-weight: 300;
}

.form-wrap{
  display: flex;
}

.survey{
  display: flex;
  flex-direction: column;
  /* min-height: 80vh; */
  justify-content: center;
  align-items: center;
  width:100%;
  position: relative;

}

body{
  scroll-behavior: smooth;
}

