.survey_question_title{
  font-size: 30px;
  font-weight: 300;
  padding:20px 30px;
  text-align: left;
  width:100%;
}

.slider_container{
  position: relative;
  margin:50px;
  width:100%;
  display: flex;
  justify-content: center;
}

.slider_wrap{
  display: flex;
  width:100%;
  justify-content: space-between;
}

.slider_dots{
  width:30px;
  height:30px;
  border-radius: 50%;
  background:white;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.slider_line{
  position: absolute;
  width:98%;
  top:15px;
  right:0%;
  border-bottom: solid 2px white;
}

.slider_dot_text{
  position: absolute;
  max-width:200px;
  min-width: 100px;
  margin-top:50px;
}

.slider_dot_selected{
  width:15px;
  height:15px;
  border-radius: 50%;
  background: orange;
  z-index: 2;
}

.textbox_container{
  width:100%;
}

.textbox_input{
  margin:40px 0px 20px 0px;
  padding:10px;
  outline:none;
  width:100%;
  height:60px;
  background: transparent;
  border-radius: 20px;
  color:white;
  font-family: 'Roboto', sans-serif;
}

.boolean_container{
  width:100%;
  display: flex;
  justify-content: flex-start;
  padding:30px;
}

.boolean_wrap{
  cursor: pointer;
  display: flex;
  margin:20px;
  align-items: center;
  font-size: 24px;
}

.boolean_radio{
  width:30px;
  height:30px;
  background:transparent;
  border:2px solid orange;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 50%;
  flex-shrink:0;
  margin:0px 10px;
}

.boolean_dot_selected{
  width:15px;
  height:15px;
  border-radius: 50%;
  background: orange;
  z-index: 2;
}


.checkbox_container{
  position: relative;
  margin:30px 80px 80px 80px;
  width:100%;
  display: flex;
  justify-content: center;
}

.checkbox_wrap{
  display: flex;
  width:80%;
  justify-content: space-between;
}

.checkbox_dots{
  width:30px;
  height:30px;
  background:transparent;
  border: 1px solid orange;
  z-index: 1;
  display: flex;
  /* align-items: center; */
  justify-content: center;
  cursor: pointer;
}


.checkbox_dot_text{
  position: absolute;
  max-width:180px;
  min-width: 100px;
  margin-top:50px;
  font-size: 14px;
}

.checkbox_dot_selected{
  display: flex;
  justify-content: center;
  align-items: center;
  background: orange;
  width:30px;
  height:30px;
}

.radio_container{
  position: relative;
  margin:20px;
  width:100%;
  display: flex;
  justify-content: left;
}

.radio_wrap{
  display: flex;
  width:80%;
  justify-content: space-between;
  flex-direction: column;
}

.radio_dots_wrap{
  display: flex;
  align-items: center;
  padding:10px;
  cursor: pointer;
}

.radio_dots{
  width:30px;
  height:30px;
  background:transparent;
  border:2px solid orange;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 50%;
  flex-shrink:0;
}


.radio_dot_text{
  /* max-width:200px; */
  width: 100%;
  /* margin-top:50px; */
  padding: 0px 20px;
  text-align: left;
}

.radio_dot_selected{
  width:15px;
  height:15px;
  border-radius: 50%;
  background: orange;
  z-index: 2;
}

.progress_dot{
  width:10px;
  height:10px;
  border-radius: 50%;
  border: 1px solid orange;
  margin:10px;
}

.progress_container{
  position: absolute;
  right:-100px;
  top:150px;
}

.thankyou_container{
  font-size: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 60vh;
}

.survey_question_info{
  font-size: 20px;
  text-align: left;
  width:100%;
  font-weight: 300;
}

.home_button{
  text-decoration: none;
  background: linear-gradient(240.71deg, #FFCB52 0%, #FF7B02 100%);
  padding:10px 20px;
  border-radius: 30px;
  font-size: 20px;
  margin:20px;
  color:white;
}

.emoticon_container{
  display: flex;
  flex-direction: column;
  text-align: left;
}

.emoticon_wrapper{
  display:flex;
  padding:20px;
  justify-content: center;
  cursor: pointer;
}

.emoticon_question_title{
  font-size: 30px;
  font-weight: 300;
  text-align: left;
  width:100%;
}

.emoticon_wrap{
  padding:10px 30px;
  border-radius: 20px;
}
